.Link_box_list_wrap{
    display: flex;
    justify-content: center;
}
.Link_box_list{
    display: grid;
    grid-template-columns: 500px 500px;
    grid-template-rows: 300px 300px;
    grid-gap: 10px;
    gap: 10px;
}
.Link_box{
    width: 100%;
    height: 100%;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Link_box_wrap{
    position: relative;
}
.Link_box_button{
    width: 70px;
    height: 70px;
    border-radius: 70px;
    outline: none;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    text-decoration: none;
}
.Link_box_button > i {
    font-size: 3em;
    color: #f54842;
}
.Link_box_wrap:hover .Link_box_hover {
    opacity: 1;
}
.Link_box_hover{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.527);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity .7s;
}
